@import "../../../theme";
.container {
  width: 50%;
  font-size: 1.68rem;
  line-height: 1.5;
  padding: 0 2.45rem 0 0;
  margin: 0 0 20px 0;
  @media (max-width: 600px) {
    width: calc(99.999999% * 0.51);
    width: 100%;
  }
  @media (min-width: 760px) {
    padding: 0 3.5rem 0 0;
  }
}
.container_item_title {
  small {
    color: var(--second-title-color);
  }
}
.container_item_title_persentage {
  color: var(--second-title-color);
  font-weight: 600;
}
.container_item_title_text {
  color: var(--primary-title-color);
  font-weight: 600;
  margin: 0 0 0 10px;
}
.container_progressBar {
  position: relative;
  width: 100%;
  height: 0.5rem;
  background-color: var(--third-black-color);
}
.container_progressBar_view {
  position: absolute;
  height: 0.5rem;
  background-color: var(--second-title-color);
  transition: width 1.4s ease-in-out 0.5s;
}
